#add{
    margin: 10px 30px 10px 30px;
    height: 40px;
    width: 300px;
    line-height: 40px;
    background-color: #664C43;
    color: beige;
    font-size: 40px;
    text-align: center;
    display: inline-block;
    font-family: 'Montserrat', sans-serif;
    border: none;
    border-radius: 5px;
}
#filter{
    height: 80px;
    line-height: 80px;
    text-align: center;
    flex-grow: 1;
    flex-basis: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-color: #664C43;
    border-style: solid;
}

#delete{
    height: 80px;
    line-height: 80px;
    text-align: center;
    flex-grow: 1;
    flex-basis: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border-color: #664C43;
    border-style: solid;
}

#delete-dropup{
    height: 80px;
    width: 100%;
    line-height: 80px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-color: #664C43;
    border-style: solid;
}

#filter-dropup{
    height: 80px;
    width: 100%;
    line-height: 80px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-color: #664C43;
    border-style: solid;
}

.dropup {
    position: relative;
    display: inline-block;
    /*display: none;*/
    flex-grow: 1.1;
    flex-basis: 0;
}

.dropup-content {
    width: 100%;
    display: flex;
    flex-direction: column;
    position: absolute;
    background-color: #ebe3ca;
    min-width: 160px;
    bottom: 80px;
    z-index: 1;
    border: #664C43;
}

.dropup-content button {
    border-top: 2px solid #664C43;
    border-left: 2px solid #664C43;
    border-right: 2px solid #664C43;
    border-bottom: 0px;
    /*border-color: #664C43;*/
    border-style: solid;
    font-size: 15px;
    font-family: 'Montserrat', sans-serif;
    /*color: black;*/
    padding: 5px 16px;
    text-decoration: none;
    display: block;
    background-color: #ebe3ca;
}

.accent {
    background-color: #C5AA8C;
}
/* The Modal (background) */
.modal {
    display: block; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    padding-top: 250px; /* Location of the box */
    left: 0;
    top: 0;
    margin: 0px;
    width: 360px; /* Full width */
    height: 390px; /* Full height */
    overflow: scroll; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}


/* Modal Content */
.modal-content {
    background-color: #fefefe;
    margin: auto;
    padding: 5px 25px 20px 25px;
    border: 1px solid #888;
    width: 250px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: stretch;
}

#modal-response{
    width: 75%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}

#modal-response button{
    background-color: #C5AA8C;
    color: #664C43;
    border: none;
    border-radius: 5px;
    font-size: 20px;
}

#delete-confirm-button {
    background-color: #E57171 !important;
}

